*{
    font-family: Poppins;
}
@media only screen and (min-width: 360px)
{
    body{
        margin: 0px;
        background-image: url("./images/background.jpg");
        background-size: cover;
        background-repeat: no-repeat;
    }
    .nav-container{
        display:flex;
        justify-content: center;
        background: white;
    }
    .logo{
        width: 20%;
    }
    .hero-images{
        display:none;
    }
    .hero-text{
        text-align: center;
        margin: 40% 7%;
        padding: 5%;
        background: rgba(255, 255, 255, 0.4);
        border-radius: 10px;
        box-shadow: 0px 5px 20px 5px #000000;
    }
}
@media only screen and (min-width: 768px)
{
    .hero-text{
        margin: 20% 10%;
    }
}
@media only screen and (min-width: 1024px)
{
    body{
        background-image: none;
        background-color: lightgray;
    }
    .app{
        width: 90%;
        margin: 5% auto 0 auto;
        border-radius: 10px;
        box-shadow: 0px 5px 20px 5px #000000;
        background-image: url("./images/background.jpg");
        background-size: cover;
        background-repeat: no-repeat;
    }
    .nav-container{
        box-shadow: 0px 3px 7px rgba(0,0,0,0.1);
        border-radius: 10px;
    }
    .hero-images{
        display: flex;
        justify-content: center;
        background: rgba(255, 255, 255, 0.4);
        margin: 10% 10%;
        border-radius: 10px;
        box-shadow: 0px 5px 20px 5px #000000;
    }
    .hero-images-column-1,.hero-images-column-2,.hero-images-column-3,.hero-images-column-4,.hero-images-column-5{
        width: 20%;
    }
    .image{
        display: block;
        width: 90%;
        height: 40%;
        margin: 10% auto;
        border-radius: 10px;
        border: 1px solid grey;
        object-fit: cover;
    }
    .image-additional{
        height: 30%;
    }
    .hero-images-column-1{
        margin-top: 25%;
    }
    .hero-images-column-2{
        margin-top: 10%;
    }
    .hero-images-column-3{
        margin-top: 20%;
    }
    .hero-images-column-4{
        margin-top: 15%;
    }
    .hero-images-column-5{
        margin-top: 30%;
    } 
}
